import React from 'react';

class Reloj extends React.Component{
    constructor(props){
        super(props);
        this.state={
            hora: new Date()
        };
    };
    render(){
        return <div>{this.state.hora.toLocaleTimeString()}</div>
    };
    componentDidMount(){
        let aSecond = 1000;
        setInterval(()=>{
            this.setState({
                hora: new Date()
            });
        },aSecond);
    };
};

export default Reloj;