import React from 'react';
import './estilosDos.css';
import './mediaqueriesDos.css';

const CuestionarioDos =()=>{

    const[index,setIndex]=React.useState(0);
    const[indexTwo,setIndexTwo]=React.useState(1);
    const[num,setNum]=React.useState(Math.floor((Math.random()-Math.random())*100));
    const[numTwo,setNumTwo]=React.useState(Math.floor((Math.random()-Math.random())*100));
    const[resultado,setResultado]=React.useState('');
    const[comprobacion,setComprobacion]=React.useState('Acá se mostrará tu resultado');
    const[contador,setContador]=React.useState(0);

    const handleQuestion =(event)=>{
        event.preventDefault();
        setIndex(index + 1);
        setIndexTwo(indexTwo + 1)
        setNum(Math.floor((Math.random()-Math.random())*100));
        setNumTwo(Math.floor((Math.random()-Math.random())*100));
        setResultado('');
        setComprobacion('');
    };

    const handleChange =(event)=>{
        setResultado(event.target.value);
    };

    const handleCheck =(event)=>{
        event.preventDefault();
        setIndexTwo(indexTwo + 1);
        if(num > numTwo && resultado == 'Mayor >'){
            setComprobacion('Correcto');
            setContador(contador + 1);
        }else if (num < numTwo && resultado == 'Menor <'){
            setComprobacion('Correcto');
            setContador(contador + 1);
        }else if (num == numTwo && resultado == 'Igual ='){
            setComprobacion('Correcto');
            setContador(contador + 1);
        }else{
            setComprobacion('Incorrecto');
        }
    };
    
    let opcionListado = [
        'Mayor >',
        'Menor <',
        'Igual ='
    ];

    return(
        <div>
            <div className='titulo-cuestionario-dos'>Cuestionario # 2</div>
            <div className='contenedor-padre-dos'>
                <div className='mensaje-inicial-dos'>¿Mayor, menor o igual?</div>
                <div className='area-pregunta-dos'>
                    <div className='itemUno'>{num}</div>
                    <div className='itemDos'>?</div>
                    <div className='itemTres'>{numTwo}</div>
                </div>
                <select value={resultado} onChange={handleChange} className='opciones-dos'>
                    <option>Elige una opción</option>
                    <option>
                        {opcionListado[0]}
                    </option>
                    <option>
                        {opcionListado[1]}
                    </option>
                    <option>
                        {opcionListado[2]}
                    </option>
                </select>
                <div className=
                {
                    index > 9
                    ? 'invisible'
                    : 'contenedor-boton-dos'
                }
                >
                    <button onClick={handleQuestion} className=
                    {
                        indexTwo % 2 !== 0
                        ? 'invisible'
                        : 'boton-dos'
                    }
                    >Siguiente</button>
                    <button onClick={handleCheck} className=
                    {
                        indexTwo % 2 == 0
                        ? 'invisible'
                        : 'boton-dos'
                    }
                    >Comprobar</button>
                </div>
                <div className=
                {
                    comprobacion == 'Acá se mostrará tu resultado'
                    ? 'comprobacion-inicio-dos'
                    :  comprobacion == 'Correcto'
                    ? 'comprobacion-correcto-dos'
                    : 'comprobacion-incorrecto-dos'
                }>
                    {
                        comprobacion == 'Acá se mostrará tu resultado'
                        ? ''
                        : comprobacion == 'Correcto'
                        ? <i class="fas fa-check-circle"></i>
                        : comprobacion == 'Incorrecto'
                        ? <i class="fas fa-times-circle"></i>
                        : ''
                    }{' '}
                {comprobacion}</div>
                <div className=
                {
                    index > 9
                    ? 'score-dos'
                    : 'invisible'
                }
                >Tu resultado final es {contador}/10</div>
            </div>
        </div>
    );
};

export default CuestionarioDos;