import React from 'react';
import './estilosUno.css';
import './mediaqueriesUno.css';

const CuestionarioUno =()=>{

    const[index,setIndex]=React.useState(0);
    const[indexTwo,setIndexTwo]=React.useState(1);
    const[resultado,setResultado]=React.useState('');
    const[comprobacion,setComprobacion]=React.useState('Acá se mostrará tu resultado');
    const[contador,setContador]=React.useState(0);

    const handleQuestion =(event)=>{
        event.preventDefault();
        setIndex(index + 1);
        setIndexTwo(indexTwo + 1)
        setResultado('');
        setComprobacion('');
    };

    const handleChange =(event)=>{
        setResultado(event.target.value);
    };

    const handleCheck =(event)=>{
        event.preventDefault();
        setIndexTwo(indexTwo + 1);
        switch(index){
            case 0:
                if(resultado=='Entero'){
                    setComprobacion('Correcto');
                    setContador(contador + 1);
                }else{
                    setComprobacion('Incorrecto');
                };
                break;
            case 1:
                if(resultado=='Conmutativa'){
                    setComprobacion('Correcto');
                    setContador(contador + 1);
                }else{
                    setComprobacion('Incorrecto');
                };
                break;
            case 2:
                if(resultado=='Inverso'){
                    setComprobacion('Correcto');
                    setContador(contador + 1);
                }else{
                    setComprobacion('Incorrecto');
                };
                break;
            case 3:
                if(resultado=='Verdadero'){
                    setComprobacion('Correcto');
                    setContador(contador + 1);
                }else{
                    setComprobacion('Incorrecto');
                };
                break;
            case 4:
                if(resultado=='Elemento neutro'){
                    setComprobacion('Correcto');
                    setContador(contador + 1);
                }else{
                    setComprobacion('Incorrecto');
                };
                break;
            case 5:
                if(resultado=='Cerradura'){
                    setComprobacion('Correcto');
                    setContador(contador + 1);
                }else{
                    setComprobacion('Incorrecto');
                };
                break;
            case 6:
                if(resultado=='Distributiva'){
                    setComprobacion('Correcto');
                    setContador(contador + 1);
                }else{
                    setComprobacion('Incorrecto');
                };
                break;
            case 7:
                if(resultado=='Todos son I'){
                    setComprobacion('Correcto');
                    setContador(contador + 1);
                }else{
                    setComprobacion('Incorrecto');
                };
                break;
            case 8:
                if(resultado=='Inverso'){
                    setComprobacion('Correcto');
                    setContador(contador + 1);
                }else{
                    setComprobacion('Incorrecto');
                };
                break;
            case 9:
                if(resultado=='Naturales'){
                    setComprobacion('Correcto');
                    setContador(contador + 1);
                }else{
                    setComprobacion('Incorrecto');
                };
                break;
        };
    };

    let preguntaListado = [
        '¿Cómo clasificarías al número -18?',
        'Dado 13 + 67 = 67 + 13, ¿qué propiedad se aplica?',
        'Dado 82 * 1/82 = 1, ¿cuál es la propiedad aplicada a la operación?',
        'Verdadero o falso: todos los números enteros son racionales',
        'Si tenemos 36 + 0 = 36, ¿qué propiedad se aplicó?',
        '¿Qué propiedad aplica para la siguiente operación: (-2)(14) ∊ R?',
        '¿Cuál es la propiedad para: (7 * 2)+(7 * 10) = 7(2 + 10)?',
        'Dados los siguientes números: pi, e y √2, ¿cuál no es irracional?',
        'Si se tiene 51 + (-51) = 0, ¿A qué propiedad nos referimos?',
        '¿Cuáles son los números que nos ayudan a contar y sumar?'
        ];
    
    let opcionListado = [
        ['Entero','Digito','Irracional'],
        ['Asociativa','Distributiva','Conmutativa'],
        ['Inverso','Elemento neutro','Cerradura'],
        ['Falso','Verdadero','Ninguno'],
        ['Conmutativa','Elemento neutro','Asociativa'],
        ['Distributiva','Inverso','Cerradura'],
        ['Cerradura','Inverso','Distributiva'],
        ['pi','Todos son I','Ninguno es I'],
        ['Inverso','Conmutativa','Elemento neutro'],
        ['Naturales','Digitos','Enteros']
    ];

    return(
        <div>
            <div className='titulo-cuestionario-uno'>Cuestionario # 1</div>
            <div className='contenedor-padre-uno'>
                <div className='area-pregunta-uno'>
                    {
                        index > 9
                        ? 'Muchas felicidades, haz terminado'
                        : preguntaListado[index]
                    }
                </div>
                <select value={resultado} onChange={handleChange} className=
                {
                    index > 9
                    ? 'invisible'
                    : 'opciones-uno'
                }
                >
                    <option>Elige una opción</option>
                    <option>
                        {
                            index > 9
                            ? ''
                            : opcionListado[index][0]
                        }
                    </option>
                    <option>
                        {
                            index > 9
                            ? ''
                            : opcionListado[index][1]
                        }
                    </option>
                    <option>
                        {
                            index > 9
                            ? ''
                            : opcionListado[index][2]
                        }
                    </option>
                </select>
                <div className=
                {
                    index > 9
                    ? 'invisible'
                    : 'contenedor-boton-uno'
                }
                >
                    <button onClick={handleQuestion} className=
                    {
                        indexTwo % 2 !== 0
                        ? 'invisible'
                        : 'boton-uno'
                    }
                    >Siguiente</button>
                    <button onClick={handleCheck} className=
                    {
                        indexTwo % 2 == 0
                        ? 'invisible'
                        : 'boton-uno'
                    }
                    >Comprobar</button>
                </div>
                <div className=
                {
                    comprobacion == 'Acá se mostrará tu resultado'
                    ? 'comprobacion-inicio-uno'
                    :  comprobacion == 'Correcto'
                    ? 'comprobacion-correcto-uno'
                    : 'comprobacion-incorrecto-uno'
                }>
                    {
                        comprobacion == 'Acá se mostrará tu resultado'
                        ? ''
                        : comprobacion == 'Correcto'
                        ? <i class="fas fa-check-circle"></i>
                        : comprobacion == 'Incorrecto'
                        ? <i class="fas fa-times-circle"></i>
                        : ''
                    }{' '}
                {comprobacion}</div>
                <div className=
                {
                    index > 9
                    ? 'score-uno'
                    : 'invisible'
                }
                >Tu resultado final es {contador}/10</div>
            </div>
        </div>
    );
};

export default CuestionarioUno;